 /* Updated CSS with Employee-prefixed class names */
.Employee-container {
    max-width: 800px;
    margin: 20px auto;
    padding: 20px;
    border: 1px solid #ddd;
    border-radius: 8px;
    background-color: #f9f9f9;
}

.Employee-section {
    margin-bottom: 30px;
}

.Employee-section:first-child h3 {
    text-align: center;
    
    margin-bottom: 15px;
}
.Employee-container h2{
    color: gray;
}
.Employee-section h3 {
    color: #333;
    margin-bottom: 15px;
    text-align: left; /* Left-align for other headings */
}

.Employee-section p {
    margin-bottom: 10px;
    line-height: 1.5;
    text-align: justify; /* Justify text for paragraphs */
}

.Employee-form {
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.Employee-form-row {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    justify-content: space-between;
}

.Employee-label {
    margin-top: 20px;
    flex: 1;
    display: flex;
    flex-direction: column;
    text-align: left;
    font-weight: bold;
}

.Employee-input {
    width: 100%;
    max-width: 250px; /* Reduced input width */
    padding: 8px;
    border: none;
    border-bottom: 1px solid #ccc; /* Bottom border only */
    margin-top: 5px;
    background-color: transparent; /* Transparent background */
    transition: border-color 0.3s;
}

.Employee-input:focus {
    border-bottom: 1px solid #005b96; /* Highlighted bottom border on focus */
    outline: none; /* Remove default outline */
}

.Employee-ul {
    margin: 10px 0;
    padding-left: 20px;
    text-align: left; /* Left-align list items */
}

.Employee-li {
    list-style-type: disc;
    margin-bottom: 5px;
}

.Employee-inline-input {
    display: inline-block;
    width: auto;
    background-color: transparent;
    border: none;
    max-width: 160px; /* Small input width */
    border-bottom: 1px solid #a2a2a2; /* Bottom border only */
    margin: 0 10px;
}

.Employee-full-width {
    display: flex;
    align-items: center;
    gap: 10px; /* Space between inline input and text */
    text-align: justify;
}

.Employee-span{

}

@media (max-width: 768px) {
  
.Employee-label {
    margin-top: 20px;
    flex: 1;
    display: flex;
    flex-direction: column;
    text-align: left;
    font-weight: bold;
    font-size: 12px;
}
.Employee-input {
    width: 100%;
    max-width: 150px; /* Reduced input width */
    padding: 8px;
    border: none;
    border-bottom: 1px solid #ccc; /* Bottom border only */
    margin-top: 5px;
    background-color: transparent; /* Transparent background */
    transition: border-color 0.3s;
}
}






