.authorization-form {
    border: 1px solid #ddd;
    border-radius: 8px;
    /* background-color: #F2F4FA; */
    padding: 20px;
    font-family: Arial, sans-serif;
    max-width: 800px;
    margin: 0 auto;

}

.authorization-header {
    text-align: center;
}

.authorization-company-info {
    color: gray;
    margin-bottom: 20px;
}

.authorization-heading {
    margin-bottom: 10px;
    text-align: center;
}

.authorization-subheading {
    font-size: 14px;
    color: #000;
    text-align: justify;
    margin-bottom: 20px;
}

.authorization-details {
    margin-top: 20px;
}

.authorization-row {
    margin-bottom: 30px;
    text-align: left;
}

.authorization-row-horizontal {
    display: flex;
    gap: 40px;
    text-align: left;
    justify-content: space-between;
}

.authorization-column {
    flex: 1;
    display: flex;
    flex-direction: column;
}

.authorization-row label {
    font-weight: bold;
    margin-bottom: 5px;
    text-align: left;
}

.authorization-row input,
.authorization-row textarea {
    padding: 8px;
    background-color: transparent;
    border: none;
    border-bottom: 1px solid #000;
    outline: none;
    font-size: 14px;
    width: 100%;
}

.authorization-row-horizontal input {
    flex: 1;
}

.authorization-row textarea {
    height: 80px;
    
    resize: vertical;
}

.authorization-textarea {
    width: 100%;
    background-color: transparent;
    height: 80px;
    border-bottom: 1px solid #000;
    outline: none;
}









.authorization-row-horizontal {
    display: flex;
    gap: 10px;
    justify-content: flex-start; 
}

.authorization-row-horizontal .radio-group {
    display: flex;
    /* align-items: center; */
    gap: 20px; /* Space between the radio buttons */
}
