
.Application2-application-form {
  padding: 20px;
  max-width: 1200px;
  margin: 20px auto;
  border: 1px solid #ddd;
  border-radius: 8px;
  background-color: #F2F4FA;
}

.Application2-header {
  text-align: center;
  margin-bottom: 20px;
}

.Application2-company-info {
  color: gray;
}

.Application2-subheading {
  color: gray;
  margin-top: 0;
  margin-bottom: 10px;
  text-align: left;
}

.Application2-equal-opportunity {
  text-align: center;
  margin-bottom: 20px;
}

.Application2-form .Application2-row {
  display: flex;
  flex-wrap: wrap;
  gap: 15px;
  margin-bottom: 15px;
}

.Application2-input-group {
  flex: 1;
  display: flex;
  flex-direction: column;
}

.Application2-input-group label {
  font-weight: bold;
}

.Application2-input-group input {
  padding: 8px;
  margin-top: 5px;
  background-color: transparent;
  border: none;
  border-bottom: 1px solid #ccc; /* Bottom border only */
}

.Application2-full-width {
  flex: 100%;
}

.Application2-checkbox-group {
  display: flex;
  flex-direction: row;
  gap: 10px;
}

.Application2-table-heading {
  color: gray;
  margin-top: 20px;
}

.Application2-education-table, .Application2-employers-table {
  width: 100%;
  border-collapse: collapse;
  margin-bottom: 20px;
}

.Application2-education-table th, .Application2-education-table td,
.Application2-employers-table th, .Application2-employers-table td {
  border: 1px solid #ddd;
  padding: 8px;
}

.Application2-education-table th, .Application2-employers-table th {
  background-color: #f2f2f2;
  text-align: left;
}

input[type="radio"], input[type="checkbox"] {
  margin-right: 5px;
}

.Application2-education-table td,
 .Application2-employers-table td {
  border: 1px solid #ddd;
  padding: 8px;
  text-align: left;
}

.Application2-authorization {
  margin-top: 20px;
  padding: 15px;
  font-size: 17px;

  color: #000000;

}
.Application2-authorization p {

  text-align: justify;

}
.Application2-authorization h3 {

color: rgb(208, 6, 6);

}


.Application2-signature-section {
  margin-top: 20px;
  padding: 15px;
  /* border-top: 2px solid #000; */
}

.Application2-signature-heading {
  font-size: 1.2em;
  margin-bottom: 10px;
}

.Application2-signature-text {
  font-size: 1em;
  color: rgb(208, 6, 6);
  
}
.Application2-signature-text p {
  font-size: 17px;
  text-align: justify;
  color: #000;
  
}



.Application2-education-table input,
.Application2-employers-table input {
 
  color: transparent;
  background-color: transparent; 
  border: none;
}

.Application2-submit-button {
  padding: 10px 25px;
  background-color: #d00000; 
  color: #ffffff;
  border: none; 
  border-radius: 5px; 
  font-size: 16px; 
 
  cursor: pointer; 
  transition: background-color 0.3s ease; 
}

.Application2-submit-button:hover {
  background-color: #a80000; 
}

.Application2-submit-button:active {
  background-color: #8a0000; 
}

.Application2-submit-button:focus {
  outline: none; 
  box-shadow: 0 0 0 3px rgba(208, 0, 0, 0.5);
}



.Application2-input-group input {

  outline: none; /* Remove default outline */
}



@media (max-width: 768px) {
  .Application2-form {
    flex-direction: column;
    padding: 20px;
  }

  .Application2-input-group {
    width: 100%;
  }

  .Application2-row {
    flex-direction: column;
  }

  .Application2-submit-button {
    width: 100%;
  }
}







































